
@use '../../styles/variables' as *;


.form-container {
  width: 50%;
  min-width: 320px;
  max-width: 475px;
  background: #fff;
  margin: 200px auto;
  box-shadow: 0px 2px 5px rgba(0,0,0,.25);
  
  form {
    display: block;
    width: 100%;
    padding: 2em;
  }
  
  h2 {
    width: 100%;
    color: lighten(rgb(253, 253, 253), 20%);
    font-family: $font-primary;
    font-size: 1.35em;
    display: block;
    background: $color-green;
    width: 100%;
    text-transform: uppercase;
    padding: .75em 1em .75em 1.5em;
    // box-shadow:inset 0px 1px 1px fadeout(white, 95%);
    border: 1px solid darken(grey, 5%);
    margin: 0;
    font-weight: 200;
  }
  
  input {
     display: block;
    width: 80%;
    margin-bottom: 2em;
    padding: .5em 0;
    border: none;
    border-bottom: 1px solid #eaeaea;
    padding-bottom: 1.25em;
    color: #757575;
    &:focus {
      outline: none;
    }
  }
  
  .btn {
    display: inline-block;
    background: $color-black;
    border: 1px solid darken($color-green, 5%);
    padding: .5em 2em;
    color: white;
    margin-right: .5em;
    box-shadow: inset 0px 1px 0px fadeout(white, 80%); 
    &:hover {
      background: lighten($color-green, 5%);
    }
    &:active {
      background: $color-green; 
      box-shadow: inset 0px 1px 1px fadeout(black, 90%); 
    }
    &:focus {
      outline: none;
    }
  }
}
